.seller-customers{
   .active{
       color: #5931ea;
   }
    .MuiPaper-root{
        position: relative;
    }
    .seller-panel{
        width: 250px;
        float: left;
    }
    .customer-panel{
        width: calc(100% - 250px);
        float: left;
    }
    .customer-list{
        .product-image{
            width: 75px;
            float: left;
            display: inline-flex;
        }
        .product-details{
            width: calc(100%  - 200px);
            float: left;
            padding: 8px 0 8px 10px;
            .view-customer{
                margin-left: 0;
                margin-top: 10px;
            }
        }
        .product-price{
            width: 125px;
            float: right;
            text-align: right;
            padding: 8px 0 8px 0;
            .range{
                width: 50px;
                float: left;  
                text-align: left;
                font-size: 12px;
                padding-top: 3px;
            }
            .cost-delivery{
               width: calc(100% - 50px); 
               float: left;
               text-align: left;  
               .cost{
                    color: #03AC13;
                    font-size: 16px;
                    font-weight: bold;
               }
               .delivery{
                font-size: 12px; 
               }
               .more-option{
                  float: right;
                  .menu-list-grow{
                    font-size: 13px;
                    justify-content: flex-end;
                    min-width: 100px;
                  }
                  .menu-list-delete{
                    border-top: 1px solid #ccc;
                    color: red;
                  }
                  .MuiSvgIcon-root{
                      opacity: .5;
                  }
                  .hoverMenu{
                      display: none;
                    position: absolute;
                    z-index: 2;
                    right: 6px;
                    background: #fff;
                    padding: 5px 10px;
                    box-shadow: 0 0 9px #4c4c4cba;
                      span{
                          display: block;
                          margin: 5px 0;
                          &:hover{
                              text-decoration: underline !important;
                          }
                      }
                  }
                  &:hover{
                      cursor: pointer;
                      > .hoverMenu{
                         display: block !important;
                      }
                  }
               }
            }
        }
    }
}
.font12{
    font-size: 12px;
}
.font-bold{
    font-weight: bold;
}
@media screen and (max-width: 700px) {
    .seller-customers{
        .seller-panel{
            width: 100%;
            float: none;
        }
        .customer-panel{
            width: 100%;
            float: none;
        }
    }
}
