.card {
  background: #fff;
  border: 1px solid #ccd4ff;
  margin-bottom: 20px;
  width: 100%; }
  .card .dropdown {
    float: right; }
  .card .card-body {
    padding-top: 0; }
  .card .card-title {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 0px; }
  .card .card-sub-title {
    font-size: 14px;
    color: #3f3f3f; }
    .card .card-sub-title.fixed {
      margin-top: 5px;
      line-height: 1;
      width: 200px; }
  .card .card-description {
    font-size: 13px;
    color: #828282;
    overflow: hidden;
    max-height: 36px;
    display: block; }

.leftThumbCard {
  overflow: hidden;
  border-radius: 6px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  flex-grow: 0;
  flex-shrink: 0;
  border: 1px solid #E9ECFA !important; }
  .leftThumbCard .card-sub-title {
    margin-left: auto;
    margin-right: 0 !important;
    font-size: 12px !important; }
  .leftThumbCard .dropdown {
    position: absolute;
    right: 8px;
    top: 15px; }
  .leftThumbCard .card-description {
    position: relative; }
  .leftThumbCard .readmore {
    font-size: 14px;
    line-height: 1.7;
    display: block; }
  .leftThumbCard .highlight-profile-pic {
    width: 2rem;
    height: 2rem;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 50%;
    margin-right: 10px; }
  .leftThumbCard .highlight-userinfo {
    display: flex;
    align-items: center;
    justify-content: flex-start; }
  .leftThumbCard .card-thumb {
    border: 10px solid #fff;
    height: 180px;
    width: 180px;
    background-size: cover;
    background-position: center center; }
  .leftThumbCard .card-body {
    width: calc(100% - 180px);
    padding: 15px;
    height: 180px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between; }
  .leftThumbCard .card-stats {
    font-size: 12px;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%; }
    .leftThumbCard .card-stats div {
      margin-right: 20px; }
    .leftThumbCard .card-stats i {
      font-size: 14px;
      margin-left: 3px;
      color: #898989;
      text-decoration: none;
      font-style: normal; }

.bloggersCard .card-head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px; }

.bloggersCard .card-head-left {
  display: flex;
  align-items: center;
  justify-content: center; }

.bloggersCard .card-head-right {
  display: flex;
  align-items: flex-start;
  justify-content: center; }

.bloggersCard .card-thumb {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-size: cover;
  background-position: center center; }

.bloggersCard .more {
  color: #bfbfbf;
  font-size: 20px !important;
  padding: 5px 10px; }

.bloggersCard .card-stats {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 80%;
  margin: 0 auto 20px; }

.articleCard .card-head {
  align-items: flex-start !important; }

.articleCard .comment-form input {
  width: 100%;
  border-radius: 10px; }

.articleCard .comment-form button {
  margin: 0;
  margin-top: -4px;
  margin-right: -6px;
  height: 38px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  z-index: 0; }

.ce-block__content {
  margin: 0 !important; }
